/**
 * Table of Contents /*
 * ------------------------------------------------------------
 *
 * This is a development CSS file which is built to a minified production
 * stylesheet in assets/built/screen.css
 *
 * 1.
 *
 * Global Styles 2.
 *
 * Layout 3.
 *
 * Special Templates 4.
 *
 * Site Header 5.
 *
 * Site Navigation 6.
 *
 * Post Feed 7.
 *
 * Single Post 7.1. Subscribe Form 7.2. Post Footer 7.3. Comments 7.4. Related
 * Posts 7.5. Floating Header 8.
 *
 * Author Template 9.
 *
 * Error Template 10. Subscribe Overlay 11. Site Footer
 */

/**
 * 1. Global - Set up the things /*
 * ----------------------------------------------------------
 */

@import "global.css";
@import url('https://fonts.googleapis.com/css?family=Lobster&subset=cyrillic,cyrillic-ext,latin-ext,vietnamese');
@import url('https://fonts.googleapis.com/css?family=Sansita');

body {
  background : #f4f8fb;
}

canvas {
  display        : block;
  vertical-align : bottom;
}

.img {
  display             : block;
  width               : 100%;
  height              : 100%;
  border-radius       : 100%;
  background-position : center center;
  background-size     : cover;
}

.hidden {
  visibility  : hidden;
  position    : absolute;
  text-indent : -9999px;
}

/**
 * 2. Layout - Page building blocks /*
 * ----------------------------------------------------------
 */

.site-wrapper {
  display        : flex;
  min-height     : 100vh;
  flex-direction : column;
}

.site-main {
  z-index   : 100;
  flex-grow : 1;
}

/* Full width page blocks */

.outer {
  position : relative;
  padding  : 0 4vw;
}

/* Centered content container blocks */

.inner {
  width     : 100%;
  max-width : 1040px;
  margin    : 0 auto;
}

/**
 * Usage:
 *
 * <div class="outer"> <div class="inner"> Centered content </div> </div>
 */

/**
 * 3. Special Template Styles /*
 * ----------------------------------------------------------
 */

@media
  (min-width : 900px) {
  .author-template .post-feed,
  .home-template .post-feed,
  .tag-template .post-feed {
    margin-top  : -70px;
    padding-top : 0;
  }

  .home-template .site-nav {
    position : relative;
    top      : -70px;
  }
}

/* 4. Site Header /* ---------------------------------------------------------- */

.site-header {
  position        : relative;
  margin          : 0;
  padding-top     : 12px;
  padding-bottom  : 12px;
  color           : #fff;
  background      : #161616 no-repeat center center;
  background-size : cover;
}

.site-header:before {
  z-index    : 10;
  content    : "";
  display    : block;
  position   : absolute;
  top        : 0;
  right      : 0;
  bottom     : 0;
  left       : 0;
  background : rgba(0,0,0,0.18);
}

.site-header:after {
  z-index    : 10;
  content    : "";
  display    : block;
  position   : absolute;
  top        : 0;
  right      : 0;
  bottom     : auto;
  left       : 0;
  height     : 80px;
  background : linear-gradient(
                 rgba(0,0,0,0.1),
                 rgba(0,0,0,0)
               );
}

.site-header.no-cover:after,
.site-header.no-cover:before {
  display : none;
}

.site-header-content {
  display         : flex;
  min-height      : 200px;
  max-height      : 450px;
  padding         : 10vw 4vw;
  text-align         : center;
  align-items        : center;
  -ms-flex-align     : center;
  -ms-flex-direction : column;
  flex-direction     : column;
  -ms-flex-pack      : center;
  justify-content    : center;
}

.site-title {
  z-index      : 10;
  margin       : 0;
  padding      : 0;
  color        : #E6E4C5;
  font-family  : 'Sansita',
                 sans-serif;
  font-size    : 19.5rem;
  font-style   : italic;
  font-weight  : 1000;
  direction    : rtl;
  unicode-bidi : bidi-override;
}

.site-title::first-letter {
  font-size : 18rem;
}

.site-description {
  z-index        : 10;
  margin         : 1em;
  padding        : 40px 0 -20px;
  color          : #fcfad9;
  font-family    : 'Lobster',
                   regular;
  font-weight    : 75;
  letter-spacing : 7.5px;
}


/* Small Screen */
@media
  (max-width : 500px) {

  .site-title {
    font-size : 16rem;
  }

  .site-title::first-letter {
    font-size : 11.5rem;
  }

  .site-description {
    font-size      : 1.6rem;
    letter-spacing : 6px;
  }
}

.ticker-move {
  display: inline-block;
  white-space: nowrap;
  padding-right: 100%;
  animation-iteration-count: infinite;
  animation-timing-function: linear;
  animation-name: ticker;
  animation-duration: 4s;
}

@keyframes ticker {
  0% { transform: translate3d(0, 0, 0); }
  100% { transform: translate3d(-60%, 0, 0); }
}

/**
 * 5. Site Navigation /*
 * ----------------------------------------------------------
 */

.site-nav {
  z-index         : 300;
  display         : flex;
  position        : relative;
  height          : 37px;
  overflow-y      : hidden;
  align-items     : flex-start;
  font-size       : 1.3rem;
  justify-content : space-between;
}

.site-nav-left {
  display     : flex;
  overflow-x  : auto;
  overflow-y  : hidden;
  align-items : center;
}

/**
 * Site Nav Hack Explanation (above):
 *
 * What's happening above: if .site-nav-left is set to overflow-x and allow
 * sideways scrolling, so that when there isn't enough space for all nav items
 * (either due to lots of nav items, or a small viewport), you can still scroll
 * side-to-side to reach them.
 *
 * The knock-on effect of this is ugly browser-scroll bars at the bottom, so
 * 80px of padding-bottom and a 40px fixed height parent (.site-nav) hides that
 * entirely. Slightly hacky code. But nice clean end-result.
 */

.site-nav-logo {
  display        : block;
  margin-right   : 24px;
  padding        : 11px 0;
  color          : #fff;
  font-size      : 1.7rem;
  font-weight    : bold;
  flex-shrink    : 0;
  line-height    : 1em;
  letter-spacing : -0.5px;
}

.site-nav-logo:hover {
  text-decoration : none;
}

.site-nav-logo img {
  display : block;
  width   : auto;
  height  : 25px;
}

.nav {
  display    : flex;
  margin     : 0 0 0 -12px;
  padding    : 0;
  list-style : none;
}

.nav li {
  display        : block;
  margin         : 0;
  padding        : 0;
  text-transform : uppercase;
}

.nav li a {
  display : block;
  margin  : 0;
  padding : 10px 12px;
  color   : #fff;
  opacity : 0.8;
}

.nav li a:hover {
  opacity         : 1;
  text-decoration : none;
}

.site-nav-right {
  display     : flex;
  height      : 40px;
  align-items : center;
  flex-shrink : 0;
}


.social-links {
  display     : flex;
  align-items : center;
  flex-shrink : 0;
}

.social-links a:last-of-type {
  padding-right : 20px;
}

.social-link {
  display         : flex;
  margin          : 0;
  padding         : 10px;
  color           : #fff;
  opacity         : 0.8;
  align-items     : center;
  justify-content : center;
}

.social-link:hover {
  opacity : 1;
}

.social-link svg {
  height : 1.8rem;
  fill   : #fff;
}

.social-link-fb svg {
  height : 1.5rem;
}

.social-link-yt svg {
  height : 2.7rem;
}

.social-link-wb svg {
  height : 1.6rem;
}

.social-link-wb svg path {
  stroke : #fff;
}

.social-link-rss svg {
  height : 1.9rem;
}

.subscribe-button {
  display       : block;
  padding       : 4px 10px;
  color         : #fff;
  opacity       : 0.8;
  border        : #fff 1px solid;
  border-radius : 10px;
  font-size     : 1.2rem;
  line-height   : 1em;
}

.subscribe-button:hover {
  opacity         : 1;
  text-decoration : none;
}

.rss-button {
  opacity : 0.8;
}

.rss-button:hover {
  opacity : 1;
}

.rss-button svg {
  height        : 2.1rem;
  margin-bottom : 1px;
  fill          : #fff;
}

@media
  (max-width : 700px) {
  .site-header {
    padding-right : 0;
    padding-left  : 0;
  }

  .site-nav-left {
    margin-right : 0;
    padding-left : 4vw;
  }

  .site-nav-right {
    display : none;
  }
}

/* 6. Post Feed /* ---------------------------------------------------------- */

.post-feed {
  display   : flex;
  position  : relative;
  margin    : 0 -20px;
  padding   : 40px 0 0 0;
  flex-wrap : wrap;
}

.post-card {
  display         : flex;
  min-height      : 300px;
  margin          : 0 20px 40px;
  overflow        : hidden;
  border-style    : solid;
  border-radius   : 5px;
  background      : #fff center center;
  background-size : cover;
  box-shadow      : rgba(39,44,49,0.06) 8px 14px 38px,
                    rgba(39, 44, 49, 0.03) 1px 3px 8px;
  flex            : 1 1 300px;
  flex-direction  : column;
  transition      : all 0.5s ease;
}

.post-card:hover {
  box-shadow : 0 0 10px rgba(39,44,49,0.10),
               0 3px 16px rgba(39, 44, 49,0.07);
  transform  : scale(1.02);
  transition : all 0.3s ease;
}

.post-card-image-link {
  display       : block;
  position      : relative;
  overflow      : hidden;
  border-radius : 5px 5px 0 0;
}

.post-card-image {
  width           : auto;
  height          : 200px;
  background      : var(--lightgrey) no-repeat center center;
  background-size : cover;
}

/* KLEINE POSTS */

.post-card-content-link {
  display  : block;
  position : relative;
  padding  : 25px 25px 0;
  color    : var(--darkgrey);
}

.post-card-content-link:hover {
  text-decoration : none;
}

.post-card-tags {
  display        : block;
  margin-bottom  : 7px;
  color          : #3998E1;
  text-transform : uppercase;
  font-size      : 1.2rem;
  font-weight    : 500;
  line-height    : 1.15em;
  letter-spacing : 0.5px;
}

.post-card-tags::before {
  content : " #";
}

.post-card-title {
  margin-top  : 0;
  font-family : "Helvetica Neue",
                Helvetica,
                Arial,
                sans-serif;
}

.post-card-content {
  display         : flex;
  font-size       : 15px;
  flex-direction  : column;
  flex-grow       : 1;
  justify-content : space-between;
}

.post-card-excerpt {
  font-family : "Helvetica Neue",
                Helvetica,
                Arial,
                sans-serif;
  font-size   : 15px;
}

.post-card-excerpt::after {
  content : "";
}

.post-card-meta {
  padding : 0 15px 15px;
}

/* ---- AUTHOR IMAGE */

.author-profile-image {
  width         : 50px;
  height        : 50px;
  margin-right  : 20px;
  border-radius : 100%;
  box-shadow    : 0 0 10px rgba(39,44,49,0.10),
                  0 3px 16px rgba(39, 44, 49,0.07);
}

.post-card-author {
  margin-right   : -50px;
  font-size      : 1.3rem;
  font-weight    : 500;
  letter-spacing : 0.5px;
}

.post-card-author::before {
  content : "by:";
}

/**
 * Special Styling for home page grid (below):
 *
 * The first (most recent) post in the list is styled to be bigger than the
 * others and take over the full width of the grid to give it more emphasis.
 * Wrapped in a media query to make sure this only happens on large viewports /
 * desktop-ish devices.
 */

@media
  (min-width : 795px) {
  .home-template .post-feed .post-card:nth-child(6n+1):not(.no-image) {
    flex           : 1 1 100%;
    flex-direction : row;
  }

  .home-template .post-feed .post-card:nth-child(6n+1):not(.no-image) .post-card-image-link {
    position      : relative;
    border-radius : 5px 0 0 5px;
    flex          : 1 1 auto;
  }

  .home-template .post-feed .post-card:nth-child(6n+1):not(.no-image) .post-card-image {
    position : absolute;
    width    : 100%;
    height   : 100%;
  }

  .home-template .post-feed .post-card:nth-child(6n+1):not(.no-image) .post-card-content {
    flex : 0 1 357px;
  }

  .home-template .post-feed .post-card:nth-child(6n+1):not(.no-image) h2 {
    font-size : 2.6rem;
  }

  .home-template .post-feed .post-card:nth-child(6n+1):not(.no-image) p {
    font-size   : 1.8rem;
    line-height : 1.55em;
  }

  .home-template .post-feed .post-card:nth-child(6n+1):not(.no-image) .post-card-content-link {
    padding : 30px 40px 0;
  }

  .home-template .post-feed .post-card:nth-child(6n+1):not(.no-image) .post-card-meta {
    padding : 0 40px 30px;
  }
}

.home-template .site-header:after {
  display : none;
}

/* Adjust some margins for smaller screens */

@media
  (max-width : 650px) {
  .post-feed {
    padding-top : 5vw;
  }

  .post-card {
    margin : 0 20px 5vw;
  }
}

/* 7. Single Post /* ---------------------------------------------------------- */

.page-template .site-main,
.post-template .site-main {
  padding-bottom : 4vw;
  background     : #fff;
}

.post-full {
  z-index  : 50;
  position : relative;
}

/* ^ Required to make .post-full-content:before/after z-index stacking work */

.post-full-header {
  max-width  : 1040px;
  margin     : 0 auto;
  padding    : 6vw 3vw 3vw;
  text-align : center;
}

.post-full-header-protools {
  max-width  : 1040px;
  margin     : 0 auto;
  padding    : 1vw 3vw 3vw;
  text-align : center;
}

@media
  (max-width : 500px) {
  .post-full-header {
    padding : 14vw 3vw 10vw;
  }
}

.post-full-meta {
  display         : flex;
  color           : var(--midgrey);
  text-transform  : uppercase;
  align-items     : center;
  font-size       : 1.4rem;
  font-weight     : 600;
  justify-content : center;
}

.post-full-meta-date {
  color : var(--blue);
}

.post-full-title {
  margin : 0;
  color  : color(var(--darkgrey) l(-5%));
}

.date-divider {
  display : inline-block;
  margin  : 0 6px 1px;
}

.post-full-image {
  height          : 800px;
  margin          : 0 -10vw -165px;
  border-radius   : 5px;
  background      : var(--lightgrey) center center;
  background-size : cover;
}

@media
  (max-width : 1170px) {
  .post-full-image {
    height        : 600px;
    margin        : 0 -4vw -100px;
    border-radius : 0;
  }
}

@media
  (max-width : 800px) {
  .post-full-image {
    height : 400px;
  }
}

.post-full-content {
  position    : relative;
  min-height  : 230px;
  margin      : 0 auto;
  padding     : 70px 100px 0;
  color       : #212529;
  background  : #fff;
  font-family : -apple-system,
                BlinkMacSystemFont,
                "Segoe UI",
                Roboto,
                "Helvetica Neue",
                Arial,
                sans-serif,
                "Apple Color Emoji",
                "Segoe UI Emoji",
                "Segoe UI Symbol",
                "Noto Color Emoji";
  font-size   : 1.6rem;
  font-weight : 400;
  line-height : 1.5;
}

.post-full-content-protools {
  position    : relative;
  min-height  : 100px;
  margin      : 0 auto;
  color       : #212529;
  background  : #fff;
  font-family : -apple-system,
                BlinkMacSystemFont,
                "Segoe UI",
                Roboto,
                "Helvetica Neue",
                Arial,
                sans-serif,
                "Apple Color Emoji",
                "Segoe UI Emoji",
                "Segoe UI Symbol",
                "Noto Color Emoji";
  font-size   : 1.6rem;
  font-weight : 400;
  line-height : 1.5;
}

.instafeed {
  position   : relative;
  min-height : 830px;
  margin     : 10px;
}

.instavideo {
  margin-right : auto;
  margin-left  : auto;
  text-align   : center;
}

@media
  (max-width : 1170px) {
  .post-full-content {
    padding : 5vw 7vw 0;
  }
}

@media
  (max-width : 800px) {
  .post-full-content {
    font-size : 1.5rem;
  }
}

.post-full-content:before {
  z-index    : -1;
  content    : "";
  display    : block;
  position   : absolute;
  top        : 15px;
  left       : -5px;
  width      : 20px;
  height     : 200px;
  background : rgba(39,44,49,0.15);
  filter     : blur(5px);
  transform  : rotate(-5deg);
}

.post-full-content:after {
  z-index    : -1;
  content    : "";
  display    : block;
  position   : absolute;
  top        : 15px;
  right      : -5px;
  width      : 20px;
  height     : 200px;
  background : rgba(39,44,49,0.15);
  filter     : blur(5px);
  transform  : rotate(5deg);
}

.no-image .post-full-content {
  padding-top : 0;
}

.no-image .post-full-content:after,
.no-image .post-full-content:before {
  display : none;
}

.kg-card-markdown {
  display        : flex;
  max-width      : 920px;
  align-items    : center;
  flex-direction : column;
}

.footnotes,
.post-full-comments,
.post-full-content blockquote,
.post-full-content dl,
.post-full-content h1,
.post-full-content h2,
.post-full-content h3,
.post-full-content h4,
.post-full-content h5,
.post-full-content h6,
.post-full-content ol,
.post-full-content p,
.post-full-content pre,
.post-full-content ul {
  min-width : 100%;
}

.post-full-content li {
  word-break : break-word;
}

.post-full-content li p {
  margin : 0;
}

.post-template .kg-card-markdown > p:first-child {
  font-size   : 1.25em;
  line-height : 1.5em;
}

.post-full-content a {
  color      : #000;
  box-shadow : var(--blue) 0 -1px 0 inset;
}

.post-full-content a:hover {
  color           : var(--blue);
  text-decoration : none;
}

.post-full-content em,
.post-full-content strong {
  color : color(var(--darkgrey) l(-5%));
}

.post-full-content small {
  display     : inline-block;
  line-height : 1.6em;
}

.post-full-content li:first-child {
  margin-top : 0;
}

.post-full-content img,
.post-full-content video {
  display   : block;
  max-width : 1040px;
  margin    : 1.5em auto;
}

@media
  (max-width : 1040px) {
  .post-full-content img,
  .post-full-content video {
    width : 100%;
  }
}

/**
 * Full bleed images (#full) Super neat trick courtesy of @JoelDrapper
 *
 * Usage (In Ghost edtior):
 *
 * ![img](/some/image.jpg#full)
 */

.post-full-content img[src$="#full"] {
  width     : 100vw;
  max-width : none;
}

/**
 * Image captions
 *
 * Usage (In Ghost editor):
 *
 * ![img](/some/image.jpg) <small>Your image caption</small>
 */

.post-full-content img + br + small {
  display       : block;
  margin-top    : -3em;
  margin-bottom : 1.5em;
}

.post-full-content iframe {
  margin : 0 auto;
}

.post-full-content blockquote {
  margin      : 0 0 1.5em;
  padding     : 0 1.5em;
  border-left : #3eb0ef 3px solid;
}

.post-full-content blockquote p {
  margin      : 0 0 1em 0;
  color       : inherit;
  font-size   : inherit;
  font-style  : italic;
  line-height : inherit;
}

.post-full-content blockquote p:last-child {
  margin-bottom : 0;
}

.post-full-content code {
  padding       : 0 5px 2px;
  border-radius : 3px;
  background    : var(--whitegrey);
  font-size     : 0.8em;
  font-weight   : 400!important;
  line-height   : 1em;
}

.post-full-content pre {
  max-width     : 100%;
  margin        : 1.5em 0 3em;
  padding       : 20px;
  color         : var(--whitegrey);
  overflow-x    : auto;
  border        : color(var(--darkgrey) l(-10%)) 1px solid;
  border-radius : 5px;
  background    : color(var(--darkgrey) l(-3%));
  font-size     : 1.4rem;
  line-height   : 1.5em;
}

.post-full-content pre code {
  padding     : 0;
  background  : transparent;
  font-size   : inherit;
  line-height : inherit;
}

.post-full-content pre code * {
  color : inherit;
}

.post-full-content .fluid-width-video-wrapper {
  margin : 1.5em 0 3em;
}

.post-full-content hr {
  margin : 4vw 0;
}

.post-full-content hr:after {
  content     : "";
  display     : block;
  position    : absolute;
  top         : -15px;
  left        : 50%;
  width       : 1px;
  height      : 30px;
  margin-left : -10px;
  background  : color(var(--lightgrey) l(+10%));
  box-shadow  : #fff 0 0 0 5px;
  transform   : rotate(45deg);
}

.post-full-content h1,
.post-full-content h2,
.post-full-content h3,
.post-full-content h4,
.post-full-content h5,
.post-full-content h6 {
  color       : color(var(--darkgrey) l(-5%));
  font-family : 'Oswald',
                sans-serif;
}

.post-full-content h1 {
  margin      : 0.5em 0 0.2em 0;
  font-size   : 4.6rem;
  font-weight : 700;
}

@media
  (max-width : 500px) {
  .post-full-content h1 {
    font-size : 2.8rem;
  }
}

.post-full-content h2 {
  margin      : 0.5em 0 0.2em 0;
  font-size   : 3.6rem;
  font-weight : 700;
}

@media
  (max-width : 500px) {
  .post-full-content h2 {
    font-size : 2.6rem;
  }
}

.post-full-content h3 {
  margin      : 0.5em 0 0.2em 0;
  font-size   : 2.8rem;
  font-weight : 700;
}

@media
  (max-width : 500px) {
  .post-full-content h3 {
    font-size : 2.2rem;
  }
}

.post-full-content h4 {
  margin      : 0.5em 0 0.2em 0;
  font-size   : 2.8rem;
  font-weight : 700;
}

@media
  (max-width : 500px) {
  .post-full-content h4 {
    font-size : 2.2rem;
  }
}

.post-full-content h5 {
  display     : block;
  margin      : 0.5em 0;
  padding     : 1em 0 1.5em;
  color       : var(--blue);
  border      : 0;
  text-align  : center;
  font-family : 'Oswald',
                sans-serif;
  font-size   : 3.2rem;
  line-height : 1.35em;
}

@media
  (min-width : 1180px) {
  .post-full-content h5 {
    width     : 100vw;
    max-width : 1060px;
  }
}

@media
  (max-width : 500px) {
  .post-full-content h5 {
    padding   : 0 0 0.5em;
    font-size : 2.2rem;
  }
}

.post-full-content h6 {
  margin      : 0.5em 0 0.2em 0;
  font-size   : 2.3rem;
  font-weight : 700;
}

@media
  (max-width : 500px) {
  .post-full-content h6 {
    font-size : 2rem;
  }
}

.footnotes-sep {
  margin-bottom : 30px;
}

.footnotes {
  font-size : 1.5rem;
}

.footnotes p {
  margin : 0;
}

.footnote-backref {
  color           : var(--blue) !important;
  box-shadow      : none !important;
  text-decoration : none !important;
  font-size       : 1.2rem;
  font-weight     : bold;
}

/* Some grouped styles for smaller viewports */

@media
  (max-width : 500px) {
  .post-full-meta {
    font-size   : 1.2rem;
    line-height : 1.3em;
  }

  .post-full-title {
    font-size : 2.9rem;
  }

  .post-full-image {
    height        : 350px;
    margin-bottom : 4vw;
  }

  .post-full-content {
    padding : 0;
  }

  .post-full-content:after,
  .post-full-content:before {
    display : none;
  }
}

/* Tables */

.post-full-content table {
  display         : inline-block;
  width           : auto;
  max-width       : 100%;
  margin          : 0.5em 0 2.5em;
  overflow-x      : auto;
  border-collapse : collapse;
  border-spacing  : 0;
  vertical-align  : top;
  font-family     : 'Oswald',
                    sans-serif;
  font-size       : 1.6rem;
  white-space     : nowrap;
}

.post-full-content table {}

.post-full-content table td:first-child {
  background-image  : linear-gradient(
                        to right,
                        rgba(255,255,255, 1) 50%,
                        rgba(255,255,255, 0) 100%
                      );
  background-repeat : no-repeat;
  background-size   : 20px 100%;
}

.post-full-content table td:last-child {
  background-image    : linear-gradient(
                          to left,
                          rgba(255,255,255, 1) 50%,
                          rgba(255,255,255, 0) 100%
                        );
  background-repeat   : no-repeat;
  background-position : 100% 0;
  background-size     : 20px 100%;
}

.post-full-content table th {
  color            : var(--darkgrey);
  background-color : color(var(--whitegrey) l(+4%));
  text-align       : left;
  text-transform   : uppercase;
  font-size        : 1.2rem;
  font-weight      : 700;
  letter-spacing   : 0.2px;
}

.post-full-content table td,
.post-full-content table th {
  padding : 6px 12px;
  border  : color(var(--whitegrey) l(-1%) s(-5%)) 1px solid;
}

/**
 * 7.1. Subscribe Form /*
 * ----------------------------------------------------------
 */

.subscribe-form {
  margin        : 1.5em 0;
  padding       : 6.5vw 7vw 7vw;
  border        : color(var(--whitegrey) l(+2%)) 1px solid;
  border-radius : 7px;
  background    : color(var(--whitegrey) l(+4%));
  text-align    : center;
}

.subscribe-form-title {
  margin      : 0 0 3px 0;
  padding     : 0;
  color       : var(--darkgrey);
  font-size   : 3.5rem;
  font-weight : 700;
  line-height : 1;
}

.subscribe-form p {
  margin-bottom  : 1em;
  color          : var(--midgrey);
  font-size      : 2.2rem;
  line-height    : 1.55em;
  letter-spacing : 0.2px;
}

.subscribe-form form {
  display         : flex;
  max-width       : 420px;
  margin          : 0 auto;
  align-items     : center;
  justify-content : center;
}

.subscribe-form .form-group {
  flex-grow : 1;
}

.subscribe-email {
  display            : block;
  -webkit-appearance : none;
  width              : 100%;
  padding            : 10px;
  color              : var(--midgrey);
  border             : color(var(--lightgrey) l(+7%)) 1px solid;
  border-radius      : 5px;
  font-size          : 1.8rem;
  font-weight        : normal;
  line-height        : 1em;
  transition         : border-color 0.15s linear;
  user-select        : text;
}

.subscribe-email:focus {
  border-color : color(var(--lightgrey) l(-2%));
  outline      : 0;
}

.subscribe-form button {
  display                : inline-block;
  height                 : 41px;
  margin                 : 0 0 0 10px;
  padding                : 0 20px;
  color                  : #fff;
  border-radius          : 5px;
  background             : linear-gradient(
                             color(var(--blue) whiteness(+7%)),
                             color(var(--blue) lightness(-7%) saturation(-10%)) 60%,
                             color(var(--blue) lightness(-7%) saturation(-10%)) 90%,
                             color(var(--blue) lightness(-4%) saturation(-10%))
                           );
  outline                : none;
  box-shadow             : 0 0 0 1px inset rgba(0,0,0,0.14);
  text-align             : center;
  text-shadow            : 0 -1px 0 rgba(0,0,0,0.1);
  font-size              : 1.5rem;
  font-weight            : 400;
  -webkit-font-smoothing : subpixel-antialiased;
  line-height            : 37px;
}

.subscribe-form button:active,
.subscribe-form button:focus {
  background : color(var(--blue) lightness(-9%) saturation(-10%));
}

@media
  (max-width : 650px) {
  .subscribe-form-title {
    font-size : 2.4rem;
  }

  .subscribe-form p {
    font-size : 1.6rem;
  }
}

@media
  (max-width : 500px) {
  .subscribe-form form {
    flex-direction : column;
  }

  .subscribe-form .form-group {
    width : 100%;
  }

  .subscribe-form button {
    width  : 100%;
    margin : 10px 0 0 0;
  }
}

/**
 * 7.2. Post Footer /*
 * ----------------------------------------------------------
 */

.post-full-footer {
  display         : flex;
  max-width       : 840px;
  margin          : 0 auto;
  padding         : 3vw 0 6vw 0;
  align-items     : center;
  justify-content : space-between;
}

.author-card {
  display     : flex;
  align-items : center;
}

.author-card .author-profile-image {
  width        : 60px;
  height       : 60px;
  margin-right : 15px;
}

.author-card-name {
  margin    : 0 0 2px 0;
  padding   : 0;
  font-size : 2rem;
}

.author-card-name a {
  color       : var(--darkgrey);
  font-weight : 700;
}

.author-card-name a:hover {
  text-decoration : none;
}

.author-card-content p {
  margin      : 0;
  color       : var(--midgrey);
  line-height : 1.3em;
}

.post-full-footer-right {
  margin-left : 20px;
  flex-shrink : 0;
}

.author-card-button {
  display       : block;
  padding       : 9px 16px;
  color         : var(--midgrey);
  border        : color(var(--midgrey) l(+20%)) 1px solid;
  border-radius : 20px;
  font-size     : 1.2rem;
  font-weight   : 500;
  line-height   : 1;
  transition    : all ease 0.2s;
}

.author-card-button:hover {
  color           : var(--blue);
  border-color    : var(--blue);
  text-decoration : none;
}

/* 7.3. Comments /* ---------------------------------------------------------- */

.post-full-comments {
  max-width : 840px;
  margin    : 0 auto;
}

/**
 * 7.4. Related posts /*
 * ----------------------------------------------------------
 */

.read-next-feed {
  display   : flex;
  margin    : 0 -20px;
  padding   : 40px 0 0 0;
  flex-wrap : wrap;
}

.read-next-card {
  display         : flex;
  position        : relative;
  margin          : 0 20px 40px;
  padding         : 25px;
  color           : #fff;
  overflow        : hidden;
  border-radius   : 5px;
  background      : var(--darkgrey) center center;
  background-size : cover;
  box-shadow      : rgba(39,44,49,0.06) 8px 14px 38px,
                    rgba(39, 44, 49, 0.03) 1px 3px 8px;
  flex            : 1 1 300px;
  flex-direction  : column;
}

.read-next-card:before {
  content       : "";
  display       : block;
  position      : absolute;
  top           : 0;
  right         : 0;
  bottom        : 0;
  left          : 0;
  border-radius : 5px;
  background    : linear-gradient(
                    135deg,
                    rgba(0,40,60,0.8) 0%,
                    rgba(0,20,40,0.7) 100%
                  );
}

.read-next-card-header {
  z-index     : 50;
  position    : relative;
  padding-top : 20px;
  text-align  : center;
}

.read-next-card-header-sitetitle {
  display     : block;
  opacity     : 0.8;
  font-size   : 1.3rem;
  line-height : 1.3em;
}

.read-next-card-header-title {
  margin         : 0;
  padding        : 0 20px;
  color          : #fff;
  font-size      : 3rem;
  line-height    : 1.2em;
  letter-spacing : 1px;
}

.read-next-card-header-title a {
  color           : #fff;
  text-decoration : none;
  font-weight     : 300;
}

.read-next-card-header-title a:hover {
  text-decoration : none;
}

.read-next-divider {
  display         : flex;
  position        : relative;
  height          : 80px;
  justify-content : center;
}

.read-next-divider svg {
  width        : 40px;
  fill         : transparent;
  stroke       : #fff;
  stroke-width : 0.5px;
}

.read-next-card-content {
  z-index   : 50;
  display   : flex;
  position  : relative;
  font-size : 1.7rem;
  flex-grow : 1;
}

.read-next-card-content ul {
  display        : flex;
  margin         : 0 auto;
  padding        : 0;
  text-align     : center;
  flex-direction : column;
  list-style     : none;
}

.read-next-card-content li {
  margin         : 0;
  padding        : 0;
  font-size      : 1.6rem;
  font-weight    : 200;
  line-height    : 1.25em;
  letter-spacing : -0.5px;
}

.read-next-card-content li a {
  display        : block;
  padding        : 20px 0;
  color          : #fff;
  border-bottom  : rgba(255,255,255,0.3) 1px solid;
  vertical-align : top;
  font-weight    : 500;
  transition     : opacity 0.3s ease;
}

.read-next-card-content li:first-of-type a {
  padding-top : 10px;
}

.read-next-card-content li a:hover {
  opacity : 1;
}

.read-next-card-footer {
  position   : relative;
  margin     : 15px 0 3px 0;
  text-align : center;
}

.read-next-card-footer a {
  color : #fff;
}

/**
 * 7.5. Floating Header /*
 * ----------------------------------------------------------
 */

.floating-header {
  z-index       : 1000;
  display       : flex;
  visibility    : hidden;
  position      : fixed;
  top           : 0;
  right         : 0;
  left          : 0;
  height        : 60px;
  border-bottom : rgba(0,0,0,0.06) 1px solid;
  background    : rgba(255,255,255,0.95);
  align-items   : center;
  transform     : translate3d(0, -120%, 0);
  transition    : all 500ms cubic-bezier(0.19, 1, 0.22, 1);
}

.floating-active {
  visibility : visible;
  transform  : translate3d(0, 0, 0);
  transition : all 500ms cubic-bezier(0.22, 1, 0.27, 1);
}

.floating-header-logo {
  margin         : 0 0 0 20px;
  overflow       : hidden;
  text-overflow  : ellipsis;
  font-size      : 1.6rem;
  line-height    : 1em;
  letter-spacing : -1px;
  white-space    : nowrap;
}

.floating-header-logo a {
  display     : flex;
  color       : var(--darkgrey);
  align-items : center;
  font-weight : 700;
  line-height : 1.1em;
}

.floating-header-logo a:hover {
  text-decoration : none;
}

.floating-header-logo img {
  max-height : 20px;
  margin     : 0 10px 0 0;
}

.floating-header-divider {
  margin      : 0 5px;
  line-height : 1em;
}

.floating-header-title {
  margin        : 0;
  color         : #2e2e2e;
  overflow      : hidden;
  text-overflow : ellipsis;
  font-size     : 1.6rem;
  font-weight   : bold;
  flex          : 1;
  line-height   : 1.3em;
  white-space   : nowrap;
}

.floating-header-share {
  display         : flex;
  padding-left    : 2%;
  align-items     : center;
  font-size       : 1.3rem;
  justify-content : flex-end;
  line-height     : 1;
}

.floating-header-share a {
  display         : flex;
  align-items     : center;
  justify-content : center;
}

.floating-header-share svg {
  width  : auto;
  height : 16px;
  fill   : #fff;
}

.floating-header-share-label {
  display      : flex;
  margin-right : 10px;
  color        : rgba(0,0,0,0.7);
  align-items  : center;
  font-weight  : 500;
  flex-shrink  : 0;
}

.floating-header-share-label svg {
  width     : 18px;
  height    : 18px;
  margin    : 0 5px 0 10px;
  stroke    : rgba(0,0,0,0.7);
  transform : rotate(90deg);
}

.floating-header-share-fb,
.floating-header-share-tw {
  display     : block;
  width       : 60px;
  height      : 60px;
  color       : #fff;
  text-align  : center;
  align-items : center;
  line-height : 48px;
  transition  : all 500ms cubic-bezier(0.19, 1, 0.22, 1);
}

.floating-header-share-tw {
  background : #33b1ff;
}

.floating-header-share-fb {
  background : #005e99;
}

.progress {
  appearance : none;
  position   : absolute;
  right      : 0;
  bottom     : -1px;
  left       : 0;
  width      : 100%;
  height     : 2px;
  color      : var(--blue);
  border     : none;
  background : transparent;
}

.progress::-webkit-progress-bar {
  background-color : transparent;
}

.progress::-webkit-progress-value {
  background-color : var(--blue);
}

.progress::-moz-progress-bar {
  background-color : var(--blue);
}

.progress-container {
  display          : block;
  position         : absolute;
  top              : 0;
  left             : 0;
  width            : 100%;
  height           : 2px;
  background-color : transparent;
}

.progress-bar {
  display          : block;
  width            : 50%;
  height           : inherit;
  background-color : var(--blue);
}

@media
  (max-width : 900px) {
  .floating-header {
    height : 40px;
  }

  .floating-header-logo,
  .floating-header-title {
    font-size : 1.5rem;
  }

  .floating-header-share-fb,
  .floating-header-share-tw {
    width       : 40px;
    height      : 40px;
    line-height : 38px;
  }
}

@media
  (max-width : 800px) {
  .floating-header-logo {
    margin-left : 10px;
  }

  .floating-header-logo a {
    color : #2e2e2e;
  }

  .floating-header-divider,
  .floating-header-title {
    visibility : hidden;
  }
}

@media
  (max-width : 450px) {
  .floating-header-share-label {
    display : none;
  }
}

/**
 * 8. Author Template /*
 * ----------------------------------------------------------
 */

.site-header-content .author-profile-image {
  z-index     : 10;
  width       : 100px;
  height      : 100px;
  margin      : 0 0 20px 0;
  flex-shrink : 0;
  /* box-shadow: rgba(255,255,255,0.1) 0 0 0 1px; */
}

.site-header-content .author-bio {
  z-index        : 10;
  max-width      : 600px;
  margin         : 5px 0 10px 0;
  opacity        : 0.8;
  font-size      : 2rem;
  font-weight    : 300;
  flex-shrink    : 0;
  line-height    : 1.3em;
  letter-spacing : 0.5px;
}

.site-header-content .author-meta {
  z-index         : 10;
  display         : flex;
  margin          : 0 0 10px 0;
  align-items     : center;
  font-family     : 'Oswald',
                    sans-serif;
  font-style      : italic;
  flex-shrink     : 0;
  justify-content : center;
}

.site-header-content .author-location svg {
  height : 1.9rem;
  stroke : #fff;
}

.site-header-content .bull {
  display : inline-block;
  margin  : 0 12px;
  opacity : 0.5;
}

.site-header-content .social-link:first-of-type {
  padding-left : 4px;
}

@media
  (max-width : 500px) {
  .site-header-content .author-bio {
    font-size      : 1.8rem;
    line-height    : 1.15em;
    letter-spacing : 0;
  }

  .author-location,
  .author-stats {
    display : none;
  }
}

/**
 * 9. Error Template /*
 * ----------------------------------------------------------
 */

.error-template .site-main {
  padding : 7vw 4vw;
}

.site-nav-center {
  display         : flex;
  text-align      : center;
  align-items     : center;
  justify-content : center;
}

.site-nav-center .site-nav-logo {
  margin-right : 0;
}

.error-message {
  text-align : center;
}

.error-code {
  margin         : 0;
  opacity        : 0.3;
  font-size      : 12vw;
  line-height    : 1em;
  letter-spacing : -5px;
}

.error-description {
  margin      : 0;
  color       : var(--midgrey);
  font-size   : 3rem;
  font-weight : 400;
  line-height : 1.3em;
}

@media
  (max-width : 800px) {
  .error-description {
    margin    : 5px 0 0 0;
    font-size : 1.8rem;
  }
}

.error-link {
  display    : inline-block;
  margin-top : 5px;
}

.error-template .post-feed {
  padding-top : 0;
}

/**
 * 10. Subscribe Overlay /*
 * ----------------------------------------------------------
 */

.subscribe-overlay {
  z-index         : 9000;
  display         : flex;
  position        : fixed;
  top             : 0;
  right           : 0;
  bottom          : 0;
  left            : 0;
  opacity         : 0;
  background      : rgba(0,25,40,0.97);
  align-items     : center;
  justify-content : center;
  transition      : opacity 200ms ease-in;
  pointer-events  : none;
}

.subscribe-overlay:target {
  opacity        : 1;
  pointer-events : auto;
}

.subscribe-overlay-content {
  z-index    : 9999;
  position   : relative;
  margin     : 0 0 5vw 0;
  padding    : 4vw;
  color      : #fff;
  text-align : center;
}

.subscribe-overlay-logo {
  position : fixed;
  top      : 23px;
  left     : 30px;
  height   : 30px;
}

.subscribe-overlay-title {
  display     : inline-block;
  margin      : 0 0 10px 0;
  font-size   : 6rem;
  line-height : 1.15em;
}

.subscribe-overlay-description {
  max-width   : 650px;
  margin      : 0 auto 50px;
  opacity     : 0.8;
  font-family : 'Oswald',
                sans-serif;
  font-size   : 3rem;
  font-weight : 300;
  line-height : 1.3em;
}

.subscribe-overlay form {
  display         : flex;
  max-width       : 500px;
  margin          : 0 auto;
  align-items     : center;
  justify-content : center;
}

.subscribe-overlay .form-group {
  flex-grow : 1;
}

.subscribe-overlay .subscribe-email {
  display            : block;
  -webkit-appearance : none;
  width              : 100%;
  padding            : 14px 20px;
  color              : var(--midgrey);
  border             : none;
  border-radius      : 8px;
  font-size          : 2rem;
  font-weight        : normal;
  line-height        : 1em;
  letter-spacing     : 0.5px;
  transition         : border-color 0.15s linear;
  user-select        : text;
}

.subscribe-email:focus {
  border-color : color(var(--lightgrey) l(-2%));
  outline      : 0;
}

.subscribe-overlay button {
  display                : inline-block;
  height                 : 52px;
  margin                 : 0 0 0 15px;
  padding                : 0 25px;
  color                  : #fff;
  border-radius          : 8px;
  background             : linear-gradient(
                             color(var(--blue) whiteness(+7%)),
                             color(var(--blue) lightness(-7%) saturation(-10%)) 60%,
                             color(var(--blue) lightness(-7%) saturation(-10%)) 90%,
                             color(var(--blue) lightness(-4%) saturation(-10%))
                           );
  outline                : none;
  box-shadow             : 0 0 0 1px inset rgba(0,0,0,0.14);
  text-align             : center;
  text-shadow            : 0 -1px 0 rgba(0,0,0,0.1);
  font-size              : 1.7rem;
  font-weight            : 400;
  -webkit-font-smoothing : subpixel-antialiased;
  line-height            : 37px;
}

.subscribe-overlay button:active,
.subscribe-overlay button:focus {
  background : color(var(--blue) lightness(-9%) saturation(-10%));
}

.subscribe-overlay-close {
  display  : block;
  position : absolute;
  top      : 0;
  right    : 0;
  bottom   : 0;
  left     : 0;
}

.subscribe-overlay-close:before {
  content    : "";
  display    : block;
  position   : absolute;
  top        : 40px;
  right      : 25px;
  width      : 30px;
  height     : 2px;
  opacity    : 0.8;
  background : #fff;
  transform  : rotate(45deg);
}

.subscribe-overlay-close:after {
  content    : "";
  display    : block;
  position   : absolute;
  top        : 40px;
  right      : 25px;
  width      : 30px;
  height     : 2px;
  opacity    : 0.8;
  background : #fff;
  transform  : rotate(-45deg);
}

.subscribe-overlay-close:hover {
  cursor : default;
}

/* 11. Site Footer /* ---------------------------------------------------------- */

.site-footer {
  position       : relative;
  padding-top    : 20px;
  padding-bottom : 60px;
  color          : #fff;
  background     : color(var(--darkgrey) l(-15%));
}

.site-footer-content {
  display         : flex;
  color           : rgba(255,255,255,0.7);
  align-items     : center;
  font-size       : 1.3rem;
  flex-wrap       : wrap;
  justify-content : space-between;
}

.site-footer-content a {
  color : rgba(255,255,255,0.7);
}

.site-footer-content a:hover {
  color           : rgba(255,255,255,1);
  text-decoration : none;
}

.site-footer-nav {
  display : flex;
}

.site-footer-nav a {
  position    : relative;
  margin-left : 20px;
}

.site-footer-nav a:before {
  content       : "";
  display       : block;
  position      : absolute;
  top           : 11px;
  left          : -11px;
  width         : 2px;
  height        : 2px;
  border-radius : 100%;
  background    : #fff;
}

.site-footer-nav a:first-of-type:before {
  display : none;
}

@media
  (max-width : 650px) {
  .site-footer-content {
    flex-direction : column;
  }

  .site-footer-nav a:first-child {
    margin-left : 0;
  }
}

/* Subscribe FORM, from MAILCHIMP */

#mc_embed_signup form {
  padding    : 10px 0 10px 0;
  text-align : center;
}

.mc-field-group {
  display : inline-block;
}

/* positions input field horizontally */

#mc_embed_signup input.email {
  display               : inline-block;
  box-sizing            : border-box;
  width                 : 350px;
  height                : 32px;
  margin                : 0;
  padding               : 0px 0.4em;
  color                 : #343434;
  border                : 1px solid #ABB0B2;
  -moz-border-radius    : 3px;
  -webkit-border-radius : 3px;
  border-radius         : 3px;
  background-color      : #fff;
  vertical-align        : top;
  font-family           : "Open Sans",
                          "Helvetica Neue",
                          Arial,
                          Helvetica,
                          Verdana,
                          sans-serif;
  font-size             : 15px;
}

#mc_embed_signup label {
  display        : block;
  padding-bottom : 10px;
  font-size      : 16px;
  font-weight    : bold;
}

#mc_embed_signup .clear {
  display : inline-block;
}

/* positions button horizontally in line with input */

#mc_embed_signup .button {
  display               : inline-block;
  box-sizing            : border-box;
  height                : 32px;
  margin                : 0;
  padding               : 0 18px;
  color                 : #fff;
  border                : none;
  -moz-border-radius    : 3px;
  -webkit-border-radius : 3px;
  border-radius         : 3px;
  background-color      : #aaa;
  font-size             : 13px;
  line-height           : 32px;
  letter-spacing        : .03em;
  transition            : all 0.23s ease-in-out 0s;
}

#mc_embed_signup .button:hover {
  background-color : #777;
  cursor           : pointer;
}

#mc_embed_signup div#mce-responses {
  top      : -1.4em;
  width    : 90%;
  float    : left;
  clear    : both;
  margin   : 0 5%;
  padding  : 0em .5em 0em .5em;
  overflow : hidden;
}

#mc_embed_signup div.response {
  z-index     : 1;
  top         : -1.5em;
  width       : 80%;
  float       : left;
  margin      : 1em 0;
  padding     : 1em .5em .5em 0;
  font-weight : bold;
}

#mc_embed_signup #mce-error-response {
  display : none;
}

#mc_embed_signup #mce-success-response {
  display : none;
  color   : #529214;
}

#mc_embed_signup label.error {
  display     : block;
  width       : auto;
  float       : none;
  margin-left : 1.05em;
  padding     : .5em 0;
  text-align  : left;
}

@media
  (max-width : 768px) {
  #mc_embed_signup input.email {
    width         : 100%;
    margin-bottom : 5px;
  }

  #mc_embed_signup .clear {
    display : block;
    width   : 100%;
  }

  #mc_embed_signup .button {
    width  : 100%;
    margin : 0;
  }
}

