/* PARTICLES STUFF */
#particles-js {
  position         : absolute;
  width            : 100%;
  height           : 108%;
  background-color : #161616;
}

/* NEWS TICKER */
@keyframes ticker {
  0% { transform: translate3d(0, 0, 0); }
  100% { transform: translate3d(-250%, 0, 0); }
}
.tcontainer{
  width: 100%;
  overflow: hidden;
  z-index: 10;
  opacity: 0.8;
  color:  #7f8082;
  font-family : "Helvetica Neue",
                Helvetica,
                Arial,
                sans-serif;
}
.ticker-wrap {
  width: 100%;
  padding-left: 100%;
  text-decoration-color: #fff;

}
.ticker-move {
  display: inline-block;
  white-space: nowrap;
  padding-right: 100%;
  animation-iteration-count: infinite;
  animation-timing-function: linear;
  animation-name: ticker;
  animation-duration: 23s;
}
.ticker-move:hover{
  animation-play-state: paused;
}
.ticker-item{
  display: inline-block;
  padding: 0 5rem;
  opacity: 1;
  color: #fff;
}
